import React,{useState} from 'react'
import  {Link, useStaticQuery , graphql } from 'gatsby'
import Loadable from '@loadable/component'
import SidebarComponents from '../../../../Global/Pages/Listing/Single/SidebarComponents'
import SingleListingSection from '../../../../Global/Pages/Listing/Single/SingleListingSection.component'
import AdvanceSearchWidgetSidebar from '../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
const OwlCarousel = Loadable(() => import('react-owl-carousel3'))
const options = {
    loop: true,
    nav: true,
	dots: false,
	navContainerClass:"carousel-btn-block banner-carousel-btn",
    animateIn:"fadeIn",
    animateOut:'slideOutDown',
    autoplayHoverPause: false,
    autoplay: false,
    margin: 2,
    smartSpeed: 1200,
    // navElements:'<div className="carousel-btn-block banner-carousel-btn"></div>',
    navText: [
      	'<span class="carousel-btn left-btn"><i class="flaticon-left-arrow-1 left"></i></span>',
      	'<span class="carousel-btn right-btn"><i class="flaticon-right-arrow right"></i></span>'
    ],
    responsive: {
        0: {
            items: 1,
        },
        480:{
            items:1,
        },
        600: {
            items: 1,
        },
        768: {
            items: 2
        },
        992: {
            items: 2
        },
        1200: {
            items: 3
        },
        1280: {
            items: 3
        }
    }
}
const ListingPropertySlider=()=>{
	const data = useStaticQuery(graphql`
		query{
			s1: file(relativePath: {eq: "property/lsslider1.jpg"}) {
				id
				childImageSharp {
				fluid {
					originalImg
				}
				}
			}
			s2: file(relativePath: {eq: "property/lsslider2.jpg"}) {
				id
				childImageSharp {
				fluid {
					originalImg
				}
				}
			}
			s3: file(relativePath: {eq: "property/lsslider3.jpg"}) {
				id
				childImageSharp {
				fluid {
					originalImg
				}
				}
			}
		}`
	)
	return(
		<section className="listing-title-area p0">
		<div className="container-fluid">
			<div className="row">
				<div className="col-lg-12 p0">
					<OwlCarousel className="listing_single_property_slider owl-theme" {...options}>
						<div className="item">
							<img className="img-fluid" src={data.s1.childImageSharp.fluid.originalImg} alt="lsslider1.jpg"/>
						</div>
						<div className="item">
							<img className="img-fluid" src={data.s2.childImageSharp.fluid.originalImg} alt="lsslider2.jpg"/>
						</div>
						<div className="item">
							<img className="img-fluid" src={data.s3.childImageSharp.fluid.originalImg} alt="lsslider3.jpg"/>
						</div>
						<div className="item">
							<img className="img-fluid" src={data.s1.childImageSharp.fluid.originalImg} alt="lsslider1.jpg"/>
						</div>
						<div className="item">
							<img className="img-fluid" src={data.s2.childImageSharp.fluid.originalImg} alt="lsslider2.jpg"/>
						</div>
					</OwlCarousel>
				</div>
			</div>
		</div>
	</section>
	)
}
function SingleV3Section() {
	const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
		document.querySelector('body').classList.toggle('body_overlay')
		document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    return (
		<>
		<section className="listing-title-area">
			<div className="container">
				<div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
				<div className="row">
					<div className="col-lg-7 col-xl-8">
						<div className="single_property_title mt30-767">
							<h2>Luxury Family Home</h2>
							<p>1421 San Pedro St, Los Angeles, CA 90015</p>
						</div>
						<div className="dn db-991">
							<div id="main2">
								<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
							</div>
						</div>
					</div>
					<div className="col-lg-5 col-xl-4">
						<div className="single_property_social_share mt20">
							<div className="spss float-left fn-400">
								<ul className="mb0">
									<li className="list-inline-item"><Link to="#"><span className="flaticon-transfer-1"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-heart"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-share"></span></Link></li>
									<li className="list-inline-item"><Link to="#"><span className="flaticon-printer"></span></Link></li>
								</ul>
							</div>
							<div className="price text-right tal-400">
								<h2>$13,000<small>/mo</small></h2>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
		<ListingPropertySlider/>
        <section className="our-agent-single pb30-991">
		<div className="container">
          <div className="row">
            <div className="col-md-12 col-lg-8">
                <SingleListingSection/>
            </div>
			<div className="col-lg-4 col-xl-4">
                <SidebarComponents/>
			</div>
            </div>  
        </div>
        </section>
       </>
    )
}

export default SingleV3Section
